@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: black;
}

.box {
  background: transparent;
  left: 50%;
  top: 50%;
  position: absolute;
  padding: 20px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.logo {
	max-width: 30vw;
}

.parallax-section {
  position: relative;
  width: 100%;
  height: 1400px;
}
.parallax-child-section {
  clip: rect(0, auto, auto, 0);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1400px;
}

#frontlashID {
  position: fixed;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  will-change: transform;
  z-index: 1;
}
.fw-main-row-proj {
  background-attachment: scroll;
  background-color: black;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
.fw-main-row-abt {
  background-attachment: scroll;
  background-image: url(/static/media/Portland.dfe6166c.png);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
.fw-main-row-cont {
  background-attachment: scroll;
  background-image: url(/static/media/savanahmap.99ff8d53.webp);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
.fw-main-row-head {
  background-attachment: scroll;
  background-image: url(/static/media/ForestBG1.6c8cacbd.webp);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

@media screen and (max-width: 2000px) {
	.image1 {
		height: 1100px;
	}
}


@media screen and (max-width: 1600px) {
  .image1{
    height: 1000px;
  }
}

@media screen and (max-width: 1440px) {
  .image1{
    height: 900px;
  }
}

@media screen and (max-width: 1366px) {
  .image1{
    height: 800px;
  }
}
@media screen and (max-width: 912px) {
	.image1, #container-div, #project-page{
		height: 1400px;
	}
}

@media screen and (max-width: 820px) {
	.image1, #container-div, #project-page{
		height: 1200px;
	}
	.logo {
		max-width: 60vw;
	}
}

@media screen and (max-width: 768px) {
	.image1, #container-div, #project-page{
		height: 1100px;
	}
}

@media screen and (max-width: 450px) {
  .image1 {
    height: 100vh;
  }
  .section-container {
    margin: auto;
  }
	.logo {
		max-width: 60vw;
	}
}

@media screen and (max-width: 300px){
	
	.box img{
		width: 80vw;
	}

}



#container {
    background-color: black;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#footer-title {
	color: white;
  font-weight: 100;
	font-family: 'Inter', sans-serif;
}

#footer-logo {
    max-width: 80px;
    margin: 10px;
}

.footer-nav {
    padding: 5px;
}

.icon {
    max-width: 60px;
    padding: 20px 0;
    margin-top: auto 0;
}

a {
    text-decoration: none;
    color: white;
    padding-left: 10px;
		font-family: 'Inter', sans-serif;
}

a:hover {
    color: #c0915f;
}

#footer-social {
    max-width: 100%;
}

@media screen and (max-width: 414px) {
    #container {
        flex-wrap: wrap;
        justify-content: center;
        text-align: center;
    }

    .footer-social {
        padding-top: 10px;
    }
}
#sqdpt-sign {
    max-width: 65%;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

#container-div {
    /* background-color: #2b4b23; */
		display: flex;
    max-width: 100%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
    margin-bottom: 50px;
    border-radius: 20px;
}

.about-p{
		font-size: 1.5rem;
		color: black;
		background-color: white;
		text-align: center;
		width: 50vw;
		margin: 0 auto;
}

@media screen and (max-width: 415px) {
    #sqdpt-sign {
        max-width: 100%;
    }
}


.contactContainer{
	display: flex;
	flex-direction: column;
	align-items: center;
	background-color: transparent;
	padding: 20px;
}

.contactBtn{
	background-color: transparent;
	color: transparent;
	padding: 10px;
	border: none;
	border-radius: 5px;
	font-size: larger;
	font-family: 'Inter', sans-serif;
	margin: 25px;
}

.contactBtn:hover{
	background-color: transparent;
	color: transparent;
}

.contactTitle{
	font-size: xx-large;
	font-family: 'Inter', sans-serif;
	margin: 25px;
}

.emailCard{
	max-width: 50vw;
}

@media screen and (max-width: 450px) {
		.emailCard {
				max-width: 90vw;
		}
}
.email-form {
    display: flex;
    justify-content: center;
    margin-top: 10em;
    margin-bottom: 10em;
}
.nav-text {
	color: white;
}

#navbar {
	background-color: #333; /* Black background color */
	position: -webkit-sticky;
	position: sticky; /* Make it stick/fixed */
	width: 100%; /* Full width */
	transition: top 0.4s; /* Transition effect when sliding down (and up) */
	z-index: 99;
}
.projectContainer{
	background-color: black;
}
.card-wrapper{
	padding-top: 20px;
	padding-bottom: 20px;
	background-color: black;
}

.carousel-container {
	background-color: black;
}

.video-wrapper{
	height: 50vh;
	justify-content: center;
}

.video-title{
	color: white;
}
.video-info{
	color: white;
}

@media screen and(max-width: 480px) {
	.carousel-container{
		margin-top: 0vh;
	}
	
}
